import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { OrdersComponent } from './orders.component';
import {CartComponent} from "./components/cart/cart.component";
import {CheckoutComponent} from "./components/checkout/checkout.component";
import {AuthGuard} from "../root/guards/auth.guard";
import {OrderSuccessComponent} from "./components/result/order-success/order-success.component";
import {OrderFailureComponent} from "./components/result/order-failure/order-failure.component";
import {OrderListComponent} from "./components/order-list/order-list.component";

const routes: Routes = [
  { path: '', component: OrdersComponent },
  { path: 'cart', component: CartComponent },
  { path: 'checkout', component: CheckoutComponent , canActivate : [AuthGuard]},
  { path: 'order-success', component: OrderSuccessComponent},
  { path: 'order-failure', component: OrderFailureComponent},
  { path: 'order-list', component: OrderListComponent},
  ];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class OrdersRoutingModule { }
