import { Component, OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
import {State} from "../../../../reducers";
import * as orderActions from '../../../actions/order.actions';
import {IProduct} from "../../../../products/models/IProduct";

@Component({
  selector: 'app-order-success',
  templateUrl: './order-success.component.html',
  styleUrls: ['./order-success.component.css']
})
export class OrderSuccessComponent implements OnInit {

  public cartItems:IProduct[] = JSON.parse(localStorage.getItem('cartItems'));
  public calcTotal(){
    let tmp = 0;
    if(this.cartItems.length === 0){
      return 0;
    }
    else{
      for(let cartItem of this.cartItems){
        tmp += (cartItem.price * cartItem.qty);
      }
    }
    return tmp;
  }
  public PRODUCT_TAX:number = 5.0;
  public calcTax(){
    return this.calcTotal() * this.PRODUCT_TAX / 100;
  }
  constructor(private store:Store<State>) { }

  ngOnInit(): void {
    this.store.dispatch(orderActions.placeOrder({
      items : this.cartItems,
      total : this.calcTotal(),
      tax : this.calcTax()
    }));
  }

}
