import { Component, OnInit } from '@angular/core';
import {select, Store} from "@ngrx/store";
import {State} from "../../../reducers";
import {IProduct} from "../../../products/models/IProduct";
import * as orderActions from '../../actions/order.actions';
import * as orderReducer from '../../reducers/order.reducer';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.css']
})
export class CartComponent implements OnInit {

  public cartItems:IProduct[] = [];
  public PRODUCT_TAX:number = 5.0;
  constructor(private store:Store<State>) { }

  // calc Total
  public calcTotal(){
    let tmp = 0;
    if(this.cartItems.length === 0){
      return 0;
    }
    else{
      for(let cartItem of this.cartItems){
        tmp += (cartItem.price * cartItem.qty);
      }
    }
    return tmp;
  }

  // calc Tax
  public calcTax(){
    return this.calcTotal() * this.PRODUCT_TAX / 100;
  }

  // calc grand Total
  public calcGrandTotal(){
    return this.calcTotal() + this.calcTax();
  }

  ngOnInit(): void {
    this.store.pipe(select(orderReducer.orderFeatureKey)).subscribe((state) => {
      localStorage.setItem('cartItems', JSON.stringify(state.cartItems));
      this.cartItems = state.cartItems;
    });

  }

  // clickIncrProductQty
  public clickIncrProductQty(cartItem){
    this.store.dispatch(orderActions.incrProductQty({product : cartItem}));
  }

  // clickDecrProductQty
  public clickDecrProductQty(cartItem){
    this.store.dispatch(orderActions.decrProductQty({product : cartItem}));
  }

  // clickDeleteCartItem
  public clickDeleteCartItem(cartItem){
    this.store.dispatch(orderActions.deleteCartItem({product : cartItem}));
  }

  // click CheckOut
  public clickCheckout(){
    // send the cartItems to server
    this.store.dispatch(orderActions.sendCartToServer({cartItems : this.cartItems}));
  }

}
