import { Component, OnInit } from '@angular/core';
import {select, Store} from "@ngrx/store";
import {State} from "../../../reducers";
import {IProduct} from "../../../products/models/IProduct";
import {IAddress} from "../../../users/models/IAddress";
import * as userActions from '../../../users/actions/user.actions';
import * as userReducer from '../../../users/reducers/user.reducer';
import * as orderActions from '../../../orders/actions/order.actions';
import * as orderReducer from '../../../orders/reducers/order.reducer';
import { loadStripe } from '@stripe/stripe-js';
import { environment } from "../../../../environments/environment";
import Axios from "axios";


@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.css']
})
export class CheckoutComponent implements OnInit {

  public PRODUCT_TAX:number = 5.0;
  public cartItems:IProduct[]=[];
  public address:IAddress;
  // stripe object
  public stripePromise = loadStripe(environment.stripe_publishable_key);

  constructor(private store:Store<State>) { }

  ngOnInit(): void {
    // get address from NGRX Store
    this.store.pipe(select(userReducer.userFeatureKey)).subscribe((state) => {
      this.address = state.user.address;
    });

    // get cartItems from NGRX Store
    this.store.pipe(select(orderReducer.orderFeatureKey)).subscribe((state) => {
      this.cartItems = state.cartItems;
    })
  }

  // calc Total
  public calcTotal(){
    let tmp = 0;
    if(this.cartItems.length === 0){
      return 0;
    }
    else{
      for(let cartItem of this.cartItems){
        tmp += (cartItem.price * cartItem.qty);
      }
    }
    return tmp;
  }

  // calc Tax
  public calcTax(){
    return this.calcTotal() * this.PRODUCT_TAX / 100;
  }

  // calc grand Total
  public calcGrandTotal(){
    return this.calcTotal() + this.calcTax();
  }

  public async displayPayNow(){
    const stripe = await this.stripePromise;
    let dataURL = `${environment.apiURL}/payment/checkout-session`;
    console.log(dataURL);
    let response = await Axios.get(dataURL);
    // When the customer clicks on the button, redirect them to Checkout.
    const result = await stripe.redirectToCheckout({
      sessionId : response.data.id
    });
  }
}
